.schedule-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
  gap: 1.5rem;
  /* background: linear-gradient(135deg, #FFE5E5 0%, #E5F9FF 100%); */
  min-height: 100vh;
  position: relative;
  box-sizing: border-box;
  justify-content: flex-start;
  max-width: 1900px;
  width: 100%;
  margin: 0 auto;
}

body {
  background: linear-gradient(135deg, #FFE5E5 0%, #E5F9FF 100%);
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.schedule-container::before {
  content: "üìö";
  position: absolute;
  top: 5%;
  left: 5%;
  font-size: 2rem;
  animation: float 4s ease-in-out infinite;
}

.schedule-container::after {
  content: "üé®";
  position: absolute;
  bottom: 5%;
  right: 5%;
  font-size: 2rem;
  animation: float 3s ease-in-out infinite;
}

.schedule-title, .schedule-container > .MuiTypography-root {
  width: 100%;
  text-align: center;
  margin-bottom: 1.5rem;
}

.schedule-title::after {
  content: "‚è∞";
  position: absolute;
  right: -40px;
  top: -10px;
  font-size: 2rem;
  animation: spin 3s linear infinite;
}

.schedule-title-row {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.schedule-name-input {
  background: transparent;
  border: none;
  outline: none;
  font-size: 2rem;
  font-weight: bold;
  width: 80px;
  color: #222;
  text-align: right;
  margin-right: 4px;
  padding: 0;
}

.schedule-name-input::placeholder {
  color: #bbb;
  font-weight: normal;
}

.circular-chart {
  width: 800px;
  height: 650px;
  border-radius: 500%;
  position: relative;
  margin: 0 auto;
  /* background-color: white; */
  /* box-shadow: 0 12px 35px rgba(0, 0, 0, 0.15); */
  transition: all 0.3s ease;
}

@keyframes float {
  0%, 100% { transform: translateY(0) rotate(0deg); }
  50% { transform: translateY(-20px) rotate(10deg); }
}

@keyframes spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

@keyframes wobble {
  0%, 100% { transform: rotate(-3deg); }
  50% { transform: rotate(3deg); }
}

@keyframes slideUp {
  from { transform: translateY(50px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.circular-chart-container {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.activity-input-container {
  width: 100%;
  max-width: 600px;
  margin: 1rem auto;
  background: white;
  padding: 1rem;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  animation: slideUp 0.3s ease-out;
}

.activity-input-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
  flex-wrap: wrap;
  justify-content: center;
}

.time-select {
  min-width: 70px !important;
}

.time-separator {
  font-size: 1.2rem;
  color: #495057;
  margin: 0 0.5rem;
}

.activity-time {
  font-size: 0.9rem;
  color: #6c757d;
  font-family: monospace;
}

.activity-item {
  background: linear-gradient(45deg, #4ECDC4, #45B7AF);
  color: white;
  padding: 10px 20px;
  border-radius: 25px;
  font-size: 1.1rem;
  font-weight: bold;
  white-space: nowrap;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
  transition: all 0.3s ease;
  font-family: 'Comic Sans MS', cursive, sans-serif;
  border: 2px solid white;
}

.activity-item:hover {
  transform: scale(1.15) rotate(-5deg);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
  background: linear-gradient(45deg, #FF6B6B, #FF5252);
}

.activity-form {
  display: flex;
  gap: 1.5rem;
  align-items: center;
  background-color: white;
  padding: 2rem;
  border-radius: 30px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
  border: 3px solid #4ECDC4;
  animation: slideUp 0.5s ease-out;
}

.activity-form input {
  padding: 1rem 1.5rem;
  border: 3px solid #4ECDC4;
  border-radius: 15px;
  font-size: 1.1rem;
  outline: none;
  transition: all 0.3s ease;
  font-family: 'Comic Sans MS', cursive, sans-serif;
  width: 200px;
}

.activity-form input:focus {
  border-color: #FF6B6B;
  transform: scale(1.05);
  box-shadow: 0 0 15px rgba(255, 107, 107, 0.3);
}

.activity-form button {
  background: linear-gradient(45deg, #FF6B6B, #FF5252);
  color: white;
  padding: 1rem 2rem;
  border: none;
  border-radius: 15px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  font-family: 'Comic Sans MS', cursive, sans-serif;
  position: relative;
  overflow: hidden;
}

.activity-form button::before {
  content: "‚ûï";
  position: absolute;
  left: -30px;
  opacity: 0;
  transition: all 0.3s ease;
}

.activity-form button:hover {
  transform: translateY(-3px) scale(1.05);
  box-shadow: 0 8px 20px rgba(255, 107, 107, 0.3);
}

.activity-form button:hover::before {
  left: 20px;
  opacity: 1;
}

.edit-button, .delete-button {
  color: #4ECDC4 !important;
  transition: all 0.2s ease;
  opacity: 0;
}

.activity-list-item:hover .edit-button,
.activity-list-item:hover .delete-button {
  opacity: 1;
}

.edit-button:hover {
  color: #FF6B6B !important;
}

.delete-button:hover {
  color: #dc3545 !important;
}

.activity-list {
  width: 100%;
  max-width: 900px;
  margin: 1rem auto;
  background: white;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.activity-list-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid #eee;
  transition: all 0.2s ease;
  overflow: visible;
}

.activity-list-item:last-child {
  border-bottom: none;
  border-radius: 0 0 15px 15px;
}

.activity-list-item:last-child:hover {
  border-radius: 0 0 15px 15px;
}

.activity-list-item:hover {
  background-color: #f8f9fa;
}

.activity-content {
  flex: 1;
}

.activity-description {
  font-size: 1.1rem;
  font-weight: 500;
  color: #495057;
  margin-bottom: 0.25rem;
}

.activity-actions {
  display: flex;
  gap: 0.5rem;
  position: relative;
  z-index: 1;
  overflow: visible;
}

.button-container {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
  width: 100%;
  justify-content: center;
  align-items: center;
  position: static;
}

.schedule-main-content {
  display: flex;
  width: 100%;
  gap: 2rem;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
  max-width: 1900px;
}

@media (min-width: 1024px) {
  .schedule-container {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    gap: 0;
    min-height: 100vh;
  }
  .schedule-main-content {
    flex-direction: row;
    gap: 3rem;
    margin-bottom: 2rem;
  }
  .schedule-main-content > .circular-chart-container {
    flex: 0 0 auto;
    align-self: center;
  }
  .schedule-main-content > div {
    flex: 1 1 0;
    min-width: 0;
    align-self: center;
  }
  .button-container {
    margin-top: auto;
    margin-bottom: 2rem;
  }
}

@media (max-width: 1023px) {
  .schedule-container {
    flex-direction: column;
    align-items: center;
    gap: 1.5rem;
    min-height: 100vh;
  }
}

@media (min-width: 1200px) {
  .schedule-main-content {
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 3rem;
  }
  .circular-chart-container {
    flex: 0 0 auto;
    align-self: flex-start;
    margin-right: 5rem;
  }
  .list-container {
    flex: 1 1 0;
    min-width: 400px;
    max-width: 700px;
    align-self: flex-start;
  }
}

@media (max-width: 1199px) {
  .schedule-main-content {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 2rem;
  }
  .circular-chart-container {
    margin-right: 0;
  }
  .schedule-main-content > div {
    max-width: 100%;
    width: 100%;
  }
  .toggle-list-btn, .activity-list {
    box-sizing: border-box;
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
  }
  .toggle-list-btn {
    display: block;
    width: 100%;
    border: 1px solid #e0e0e0 !important;
    border-bottom: none !important;
    border-radius: 15px 15px 0 0 !important;
    background: linear-gradient(45deg, #4ECDC4, #45B7AF);
    font-size: 1.1rem;
    font-weight: bold;
    padding: 1rem 0 1.25rem 0 !important;
    cursor: pointer;
    transition: background 0.2s, border 0.2s, box-shadow 0.2s;
    text-align: center;
    margin-bottom: 1px !important;
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.075);
    z-index: 10;
    position: relative;
    touch-action: manipulation;
  }
  .toggle-list-btn:active, .toggle-list-btn:focus {
    background: linear-gradient(45deg, #FF6B6B, #FF5252);
    outline: none;
  }
  .toggle-list-btn.closed {
    border-radius: 15px !important;
    border-bottom: 1px solid #e0e0e0 !important;
  }
  .activity-list {
    border-radius: 0 0 15px 15px !important;
    border-top-left-radius: 0 !important;
    border-top-right-radius: 0 !important;
    border-top: none !important;
    border: 1px solid #e0e0e0;
    margin-top: 0;
    padding-top: 0;
  }
}

.center-circle-cta {
  fill: #FFE066;
  stroke: #4ECDC4;
  stroke-width: 6px;
  filter: drop-shadow(0 0 12px #FFD166) drop-shadow(0 0 4px #fff);
  animation: pulse-center 1.2s infinite;
  cursor: pointer;
  transition: filter 0.2s, stroke 0.2s, fill 0.2s;
}

@keyframes pulse-center {
  0%, 100% { filter: drop-shadow(0 0 12px #FFD166) drop-shadow(0 0 4px #fff); }
  50% { filter: drop-shadow(0 0 24px #FFD166) drop-shadow(0 0 8px #fff); }
}

.activity-list-item.highlighted {
  background: #FFF9C4;
  transition: background 0.6s;
}

.activity-list-item.glowing {
  background: #FFFDE7 !important;
  box-shadow: 0 0 8px 2px rgba(255, 214, 0, 0.25), 0 0 0 1.5px #FFE066;
  transition: background 0.2s, box-shadow 0.2s;
  z-index: 2;
  position: relative;
}

.circular-chart text {
  font-family: 'Noto Sans KR', 'Nanum Gothic', 'Malgun Gothic', 'Apple SD Gothic Neo', 'ÎßëÏùÄ Í≥†Îîï', 'ÎèãÏõÄ', Arial, sans-serif;
}

.edit-button, .delete-button {
  pointer-events: auto;
  margin: 0 2px;
}