* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.main-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  gap: 2rem;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  padding: 1rem;
}

.main-title {
  font-size: 2.5rem;
  color: #495057;
  text-align: center;
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.start-button {
  background-color: #6c757d;
  color: white;
  padding: 1rem 2rem;
  border: none;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: 500;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.start-button:hover {
  background-color: #5a6268;
} 